@import '../../assets/fonts/ComicNeue/ComicNeue.scss';

.status-bar-container {
	width: 380px;
	height: 98px;
	background-color: rgb(197, 189, 172);
	border-radius: 17px;
	display: flex;
	justify-content: center;
	align-items: center;
	box-shadow: 2px 2px 2px 2px rgb(162, 151, 129);

	p {
